@use 'base/variables' as v;
@use 'base/mixins' as m;

.categories-section {
  h1{
    margin: 2rem 0;
  }
  .categories-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;

    @include m.tablet{grid-template-columns: repeat(2, 1fr);}
    @include m.phone{grid-template-columns: 1fr;}

    .about-poll-container {
      position: relative;
      height: 100%;

      .about-poll {
        background-color: rgba($color: v.$black, $alpha: 0.8);
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        padding: .7rem 1rem;

        h3 {
          text-align: start;
          font-size: 3rem;
        }
      }
    }
  }
}